.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 360px;
  margin-bottom: 56px;

  @include onDesktop {
    width: 212px;
  }

  &__foto {
    width: 166px;
    height: 166px;
    border-radius: 20px;
    margin: 0 0 32px;
    object-fit: cover;
  }

  &__title {
    font-family: "Poppins", sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 140%;
    text-align: center;
    color: #253757;
    margin-bottom: 16px;
  }

  &__text {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 150%;
    text-align: center;
    color: #6c788b;
  }
}
