.links {
  display: flex;
  font-weight: bold;
  font-size: 13px;
  line-height: 150%;
  border-bottom: 2px solid transparent;
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
  letter-spacing: 3px;
  transition: 0.5s;
  padding: 0;
  height: 24px;

  &:hover {
    border-color: #2060f6;
  }
}
