.button {
  display: flex;
  justify-content: center;
  text-align: center;
  width: 100%;
  height: 50px;
  line-height: 50px;
  text-decoration: none;
  color: #fff;
  background-color: #2060f6;
  border-radius: 8px;
  margin-bottom: 48px;

  @include onTablet {
    width: 264px;
    margin-bottom: 146px;
  }
}
