.slider {
  position: absolute;
  right: 0;
  bottom: 0;
  height: 390px;
  width: 100%;
  background-image: url(../images/Photo.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  border-radius: 30px;

  @include onDesktop {
    bottom: 0;
    width: 49%;
    height: 680px;
    border-radius: 30px 0;
  }

  @include hdDesktop {
    width: 43%;
  }

  &__container {
    position: absolute;
    left: 20px;
    bottom: 24px;

    @include onTablet {
      display: flex;
      left: 40px;
      bottom: 32px;
    }

    @include hdDesktop {
      left: 76px;
      bottom: 64px;
    }
  }

  &__buttons {
    display: flex;
    gap: 16px;
    margin-bottom: 16px;
    margin-right: 24px;
  }

  &__button {
    display: block;
    height: 32px;
    width: 32px;
    line-height: 32px;
    font-size: 20px;
    text-align: center;
    border-radius: 50%;
    color: #253757;
    background-color: #fff;
  }

  &__title {
    font-weight: bold;
    font-size: 13px;
    line-height: 19px;
    letter-spacing: 3px;
    text-transform: uppercase;
    margin-bottom: 8px;
  }

  &__text {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 140%;
  }
}
