.nav {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 48px;

  @include onDesktop {
    margin-bottom: 164px;
  }

  &__list {
    font-family: "Poppins", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 13px;
    line-height: 150%;
    text-align: center;
    letter-spacing: 3px;
    text-transform: uppercase;
    list-style: none;
    display: none;

    @include onDesktop {
      display: flex;
    }
  }

  &__item {
    margin-right: 48px;
    // padding-bottom: 8px;
  }
  

  &__logo {
    display: flex;
  }
  &__logo-img {
    display: flex;
    height: 26px;
    margin-right: 36px;
  }

  &__container {
    display: flex;
  }

  &__menuOpener {
    @include onDesktop {
      display: none;
    }
  }
}
