.expertise {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border-radius: 30px;
  padding: 72px 20px;

  @include onTablet {
    padding: 128px 20px;
  }

  &__title {
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: 44px;
    line-height: 120%;
    text-align: center;
    color: #253757;
    margin-bottom: 56px;
  }

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 1148px;
    margin: 0 auto;

    @include onDesktop {
      flex-direction: row;
    }
  }
}
