.aboutUs {
  padding: 148px 20px;

  &__title {
    font-family: "Poppins", sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 32px;
    line-height: 150%;
    color: #253757;
    text-align: center;
    padding-bottom: 24px;
    margin-bottom: 16px;
  }

  &__text {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 150%;
    text-align: center;
    color: #6c788b;
    max-width: 558px;
    margin: auto;

    @include onDesktop {
      font-size: 24px;
      max-width: 716px;
    }

    @include hdDesktop {
      max-width: 754px;
    }
  }
}
