.header {
  position: relative;
  padding: 20px;
  background-color: #2c2c2c;
  box-sizing: border-box;
  height: 873px;
  border-radius: 0 0 30px 30px;

  @include onDesktop {
    height: 800px;
  }

  &__container {
    margin: auto;
    max-width: 968px;
  }

  &__title {
    margin: 0 0 32px;
    text-align: left;
    color: #fff;
    font-weight: 600;
    font-size: 52px;
    line-height: 120%;
    letter-spacing: -2px;

    @include onTablet {
      margin-bottom: 24px;
      font-size: 64px;
    }

    @include onDesktop {
      max-width: 400px;
      margin-bottom: 32px;
    }
  }

  &__text {
    font-family: "Open Sans", sans-serif;
    font-size: 18px;
    line-height: 150%;
    margin: 0 0 72px;
    max-width: 362px;

    @include onTablet {
      margin-bottom: 56px;
    }

    @include onDesktop {
      margin-bottom: 84px;
    }
  }
}
